/* FRAMES THE WEB APPLICATION */
body {
  position: absolute;
  width: 80%;
  left: 10%;
  top: 5%;
}

/* SCREENS */
#todo_home {
  display: grid;
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  padding: 5px 10px 5px 10px;
}
#todo_list {}
#todo_item {}

/* HOME SCREEN CONTROLS */
#home_your_lists_container {
  grid-column: 1 / span 1;
  grid-row: 1 / span 2;
}
#home_your_lists_heading {}
#home_your_lists_list {}
#home_banner_container {
  grid-column: 2 / span 2;
  grid-row: 1 / span 1;
  cursor: default;
}
#home_new_list_container {
  grid-column: 2 / span 2;
  grid-row: 2 / span 1;
  display: flex;
}
#home_new_list_button, .item_button {
  margin: auto;
  border-radius: 10px;
  cursor: pointer;
}

/* HOME SCREEN CLASSES */
.home_list_link {}

/* LIST SCREEN CONTROLS */
#list_heading {}
#list_trash {
  position: absolute;
  left: 85%;
  width: 15%;
  text-align: right;
  top: 10px;
}
#list_details_container {
  padding-left: 2%;
  display: grid;
  grid-template-columns: 40% 1fr;
  grid-gap: 5px;
  margin-top: 1%;
  margin-bottom: 1%;
}
#list_details_name_container {}
#list_name_prompt {}
#list_name_textfield {}
#list_details_owner_container {}
#list_owner_prompt {}
#list_owner_textfield {}
#list_items_container {}

/* LIST SCREEN CLASSES */
.list_item_card, .list_item_add_card, .list_item_header_card {
  position: relative;
  height: 50px;
  grid-row-gap: 0px;
  grid-column-gap: 50px;
  margin-top: 0px;
  margin-bottom: 5px;
  border-radius: 5px;
  cursor: pointer;
}
.list_item_card_description {
  position: absolute;
  left: 2%;
}
.list_item_task_header, .list_item_due_date_header, .list_item_status_header {
  position: absolute;
  top: 5%;
}
.list_item_card_assigned_to, .list_item_card_due_date, 
.list_item_card_completed, .list_item_card_not_completed {
  position: absolute;
  top: 50%;
}
.list_item_task_header, .list_item_card_assigned_to {
  left: 2%;
}
.list_item_due_date_header, .list_item_card_due_date {
  left: 35%;
}
.list_item_status_header, .list_item_card_completed, .list_item_card_not_completed {
  left: 60%;
}
.list_item_card_toolbar {
  position: absolute;
  left: 85%;
  width: 15%;
  top: 5%;
  height: 90%;
  display: grid;
  grid-template-columns: 33% 33% 33%;
}
.list_item_card_button {
  text-align: center;
  border-radius: 5px;
  height: 90%;
  margin: 0.1%;
  position: relative;
  left: 82%;
  border-style: solid;
  background-color: var(--swatch-accent);
}
.list_item_add_card {
  text-align: center;
  vertical-align: center;
  left: 47%;
  border: none;
}
.disabled{
  background-color: var(--swatch-foundation);
}

/* ITEM SCREEN CONTROLS */
#item_heading {
  font-weight: bold;
  font-size: larger;
}
#item_form_container {
  display: grid;
  grid-gap: 23px;
  grid-template-columns: 20% auto;
}
#item_description_prompt {}
#item_description_textfield {}
#item_assigned_to_prompt {}
#item_assigned_to_textfield {}
#item_due_date_prompt {}
#item_due_date_picker {}
#item_completed_prompt {}
#item_completed_checkbox {
  right: 49.3%;
  height: 3%;
}
#item_form_button{
  padding-top: 2%;
}
#item_form_submit_button {
  font-size: larger;
  border-style: outset;
  border-radius: 7px;
}
#item_form_cancel_button {
  font-size: larger;
  border-style: outset;
  border-radius: 7px;
}

/* ITEM SCREEN CLASSES */
.item_prompt {
  grid-column: 1 / 2;
  position: relative;
  font-weight: bold;
}
.item_input {
  grid-column: 2 / 4;
  position: relative;
  height: 30px;
  font-size: large;
}

/* Dialog */
#modal_yes_no_dialog{
  top: 182px;
  height: 190px;
  z-index: 1;
  position: absolute;
  left: 33%;
  background: #EED;
  width: 33%;
}
#dialog_yes_button{
  height: 26%;
  width: 13%;
}
#dialog_no_button{
  height: 26%;
  width: 13%;
}
#the_block{
  height: 100%;
  width: 100%;
  z-index: 1;
  position: absolute;
  background: black;
  top: 0;
  opacity: 0.1;
}